.cart-image {
  width: 80px;
  height: auto;
  margin: 1rem;
}

.basket-item {
  display: flex;
  margin-top: 1rem;
}

.quantity-bar {
  display: flex;
  align-items: baseline;
}

.quantity-bar input {
  margin: 0.5rem;
  appearance: textfield;
  width: 30px;
  text-align: center;
}

.change-quant {
  cursor: pointer;
}
